import {common} from "../../utils/api/url";

import mixins from "~/utils/mixins";
export default {
  name: 'appIndex',
  layout: 'defaultAppScreen',
  mixins: [mixins],
  data(){
    return {
      active: 6,
      serverAppList: [],
      noticeAppList: [],
      serchName: '',
      defaultMenuActive: '',
      dormTreeList: [],
      departmentPath: '',
      departmentName: '',
      activeTypeText: '类别',
      isCollapse: false,
      leftHeight: {
        'height': '100%',
        'width': '0%'
      },
      toggleTag: {
        'left': '0'
      }
    }
  },
  mounted() {
    this.$nextTick(() => {

    });
    document.addEventListener("click", this.bodyCloseMenus);
  },
  beforeDestroy() {
    document.removeEventListener("click", this.bodyCloseMenus);
  },
  created() {
    this.active = this.$route.query.activeType ? this.$route.query.activeType : 6;
    this.getDeptInfo(2);
    this.initCategoryList();
    if (this.active == 6){
      this.initAppRecommend();
    }else {
      this.initAppServer();
    }
  },
  methods: {
    layoutInit(){

    },
    bodyCloseMenus(event){
      if (event.target.className.indexOf("info-block-right") != -1
        || event.target.className.indexOf("van-grid") != -1
        || event.target.className.indexOf("van-grid") != -1
        || event.target.className.indexOf("fa-times-circle") != -1
        || event.target.className.indexOf("header-class") != -1
        || event.target.className.indexOf("sub-menu-class") != -1
        || event.target.className.indexOf("info-block-header") != -1
        || event.target.className.indexOf("info-block-header-text") != -1
        ){
        this.isCollapse = false;
        this.toggleLeftMenu();
      }
    },
    initAppRecommend(value){
      let params = {
        appletId: this.appletId,
        categoryId: this.categoryId,
        departmentPath: this.departmentPath,
        formType: '',
        searchKey: this.searchKey
      };
      this.$axios.get(common.server_list_list3, {params: params}).then(res => {
        if (res.data.data){
          this.serverAppList = res.data.data;
        }
      });
    },
    initAppServer(value){
      let params = {
        appletType: this.active,
        searchKey: value
      };
      this.$axios.get(common.server_list_list, {params: params}).then(res => {
        if (res.data.data){
          this.serverAppList = res.data.data;
        }
      });
    },
    initServer(data){
      let params = {
        appletId: this.appletId,
        appletType: this.active,
        categoryId: this.categoryId,
        departmentPath: this.departmentPath,
        formType: '',
        searchKey: this.searchKey
      };
      this.$axios.get(common.server_list_list, {params: params}).then(res => {
        if (res.data.data){
          this.serverAppList = res.data.data;
        }
      });
    },
    serverBlock(event, item){
      if (item == 'all'){

      }else {
        // this.$router.push({
        //   path: '/app/appServer',
        //   query: {
        //     id: item.id,
        //     activeType: this.active,
        //     userType: this.loginUserAppType,
        //     navH: this.navHeight,
        //     appType: this.globalAppShow,
        //     page: '/app/appAllServer'
        //   }
        // });
        this.$router.push({
          path: '/app/appServerForm',
          query: {
            id: item.id,
            activeType: this.active,
            userType: this.loginUserAppType,
            navH: this.navHeight,
            appType: this.globalAppShow,
            page: '/app/appAllServer'
          }
        });
      }
    },
    activeTabMenu(name){
      this.noticeAppList = [];
      this.serverAppList = [];
      this.serchName = '';
      if (name == 5){
        this.$router.push({
          path: '/app/appMyNotice',
          query: {
            id: parseInt(name),
            userType: this.loginUserAppType,
            navH: this.navHeight,
            appType: this.globalAppShow
          }
        });
      }else if (name == 6){
        this.active = name;
        this.initAppRecommend();
      }else if (name == 7){
        this.active = name;
        this.$router.push({
          path: '/app/appStatic',
          query: {
            id: parseInt(name),
            userType: this.loginUserAppType,
            navH: this.navHeight,
            appType: this.globalAppShow,
            sessionId: this.$route.query.sessionId,
            page: '/app/appAllServer'
          }
        });
      }else {
        this.active = name;
        this.initAppServer();
      }
    },
    selMenu(event, item, index){
      this.departmentPath = item.department_path;
      this.appletType = this.active;
      this.defaultMenuActive = index + '';
      this.departmentName = item.department_name;
      if (this.active == 6){
        this.initAppRecommend();
      }else {
        this.initServer();
      }
    },
    returnIndex(){
      this.$router.push({
        path: '/app/appIndex',
        query: {
          activeType: 6,
          userType: this.loginUserAppType,
          navH: this.navHeight,
          appType: this.globalAppShow,
          sessionId: this.$route.query.sessionId
        }
      });
    },
    onSearch(value){
      console.log(value);
      // if (this.active == 6){
      //   this.initAppRecommend(value);
      // }else {
      //   this.initAppServer(value);
      // }
      this.searchKey = this.serchName;
      if (this.active == 6){
        this.initAppRecommend(value);
      }else {
        this.initServer(value);
      }
    },
    dropdownItem(item){
      this.categoryId = item;
      if (this.active == 6){
        this.initAppRecommend();
      }else {
        this.initServer();
      }
      this.isCollapse = false;
      this.toggleLeftMenu();
    },
    onClear(){
      this.searchKey = "";
      this.serchName = "";
      if (this.active == 6){
        this.initAppRecommend(this.searchKey);
      }else {
        this.initServer(this.searchKey);
      }
    },
    toggleLeftMenu(event){
      this.isCollapse = false;
      this.leftHeight.width = "0%";
      this.toggleTag['left'] = "0px";
    },
    toggleRightMenu(event){
      this.isCollapse = true;
      this.leftHeight.width = "50%";
      this.toggleTag['left'] = "50%";
    },
    clearSearchDept(){
      this.departmentPath = "";
      this.defaultMenuActive = '';
      this.page = 1;
      this.tableData = [];
      this.departmentName = "";
      if (this.active == 6){
        this.initAppRecommend();
      }else {
        this.initServer();
      }
    }
  }
}
